.results-condition {
  display: flex;
  width: 210px;
  margin: 0 auto;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    width: 591px;
  }
}

.results-condition__button {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  border: none;
  background: transparent;
  min-width: 49px;
  min-height: 30px;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding: 1px 0;
    border-bottom: 1px dotted rgba(0, 0, 0, 0.3);
  }
}

.results-condition__button--before {
  &::before {
    content: "";
    position: absolute;
    top: 6px;
    left: 58px;
    display: block;
    width: 86px;
    height: 14px;
    background-color: #ffffff;
    border-radius: 6px;
    border: 1px solid #cdcdcd;

    @media (min-width: $tablet-width) {
      top: 12px;
      left: 80px;
      display: block;
      width: 426px;
      height: 6px;
      background-color: #dcdcdc;
      border-radius: 6px;
      border: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 11px;
    left: 63px;
    display: block;
    width: 36px;
    height: 6px;
    background-color: $basicgreen;
    border-radius: 6px;

    @media (min-width: $tablet-width) {
      top: -1px;
      left: 280px;
      display: block;
      width: 34px;
      height: 34px;
      border-radius: 50%;
      box-shadow: inset 0 0 0 12px #ffffff, 0 0 1px 2px #dcdcdc;
    }
  }
}
