.food-card {
  display: grid;
  grid-template-columns: 140px 141px;

  &:not(:last-child) {
    margin-bottom: 19px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.food-card__image {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  display: block;
  margin: auto;
}

.food-card__name {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: #222222;
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  width: 76px;
}

.food-card__properties {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: 50px 91px;
}

.food-card__property {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.food-card__meaning {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.food-card__order {
  display: inline-block;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 109px;
  grid-column: 1 / -1;
  grid-row: 3 / 4;
}
