.main-welcome {
  background: linear-gradient(#68b738 30%, #ffffff 40%, #ffffff 100%);
  background-image: url("../img/rectangle-opacity.png"), url("../img/background-main.jpg");
  background-position: top;
  background-repeat: no-repeat;
  padding: 27px 20px 10px 20px;

  @media (min-width: $tablet-width) {
    padding: 62px 237px 10px 117px;
    min-height: 850px;
    background-image: url("../img/img-tablet/main-can.png");
    background-position: 0 265px;
  }

  @media (min-width: $desktop-width) {
    padding: 170px 110px 10px 110px;
    min-height: 530px;
    background-image: url("../img/img-desktop/main-can.png"), url("../img/img-desktop/rectangle.png"), url("../img/img-desktop/cat-background.jpg");
    background-position: 550px 180px, 720px 0, 720px 0;
    margin-top: -150px;
  }
}

.main-welcome__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  width: 250px;
  margin: 0 0 25px 16px;
  padding: 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
    width: 416px;
    margin: 0 0 41px 0;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: 40px 0 40px 80px;
  }
}

.main-welcome__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  width: 198px;
  margin: 0 0 29px 40px;

  @media (min-width: $tablet-width) {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    width: 416px;
    margin: 0 0 49px 0;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 52px 80px;
  }
}

.main-welcome__button {
  box-sizing: border-box;
  display: inline-block;
  background-color: $basicgreen;
  width: 280px;
  padding: 7px 44px;
  color: #ffffff;

  @media (min-width: $tablet-width) {
    max-width: 244px;
    padding: 14px 26px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 80px;
  }
}

.main-welcome__button-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }
}


.main-welcome__image {
  @media (min-width: $tablet-width) {
    display: none;
  }
}
